/* styles for board */
.board {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
/* row style */
.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.cell {
  width: 50px;
  height: 50px;
  border: 1px solid black;
  background-color: white;
  margin: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
}

p {
  font-size: 30px;
  font-weight: bold;
  color: black;
  /* middlle */
  text-align: center;

}

button {
  /* center button */
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  color: black;
  background-color: white;
  border: 1px solid black;
  margin: 0 auto;
  /* middle */
  display: block;
}

.t {
  font-size: 30px;
  font-weight: bold;
  color: black;
  /* middlle */
  text-align: center;
}